import React from "react";
import {
  Box,
  Typography,
  Grid,
  Divider,
  IconButton,
  Paper,
  Stack,
} from "@mui/material";
import {
  Instagram,
  // LinkedIn,
  // Facebook,
  PhoneIphone,
  Email,
  LocationOn,
} from "@mui/icons-material";

const ContactUs = () => {
  return (
    <Box
      sx={{
        padding: { xs: "20px", md: "60px" },
        bgcolor: { xs: "transparent", md: "#f5f5f5" }, // Remove bgcolor on mobile
      }}
    >
      {/* Contact Header */}
      <Typography
        variant="h6"
        sx={{
          textAlign: "center",
          mb: 4,
          fontWeight: 600,
          color: "#022e69",
          fontSize: { xs: "1.6rem", md: "2rem" },
          letterSpacing: 1,
        }}
      >
        CONTACT US
      </Typography>

      {/* Contact Details */}
      <Grid
        container
        spacing={4}
        justifyContent="center"
        alignItems="flex-start"
      >
        {/* Address 1 */}
        <Grid item xs={12} md={4}>
          <Paper
            elevation={3}
            sx={{
              padding: 3,
              borderRadius: 2,
              boxShadow: "0 0 10px 4px rgba(239, 154, 154, 0.5)",
            }}
          >
            <Typography
              variant="h6"
              sx={{ color: "#1976d2", mb: 2, fontSize: "1.2rem" }}
            >
              <LocationOn
                sx={{
                  verticalAlign: "middle",
                  marginRight: 1,
                  color: "#145ea8",
                }}
              />
              Address 1
            </Typography>
            <Divider sx={{ mb: 2, bgcolor: "#1976d2" }} />
            <Typography variant="body2" sx={{ lineHeight: 1.6 }}>
              112, Kanakanandhal, <br />
              Thirukovilur, <br />
              Kallakurichi - 605667.
            </Typography>
          </Paper>
        </Grid>

        {/* Contact Information */}
        <Grid item xs={12} md={4}>
          <Paper
            elevation={3}
            sx={{
              padding: 3,
              borderRadius: 2,
              boxShadow: "0 0 10px 4px rgba(239, 154, 154, 0.5)",
            }}
          >
            <Typography
              variant="h6"
              sx={{
                color: "#1976d2",
                mb: 2,
                textAlign: "center",
                fontSize: "1.2rem",
                fontWeight: 600,
              }}
            >
              Contact Information
            </Typography>
            <Divider sx={{ mb: 2, bgcolor: "#1976d2" }} />

            {/* Email */}
            <Stack
              direction="row"
              alignItems="center"
              justifyContent="center"
              spacing={1}
              mb={2}
            >
              <Email sx={{ color: "#145ea8" }} />
              <Typography sx={{ fontWeight: 500 }}>
                smin.eservices@gmail.com
              </Typography>
            </Stack>

            {/* Phone Numbers */}
            <Stack spacing={2} alignItems="center">
              <Stack direction="row" alignItems="center" spacing={1}>
                <PhoneIphone sx={{ color: "#145ea8" }} />
                <Typography>6385473308</Typography>
              </Stack>
              <Stack direction="row" alignItems="center" spacing={1}>
                <PhoneIphone sx={{ color: "#145ea8" }} />
                <Typography>9385701440</Typography>
              </Stack>
            </Stack>
          </Paper>
        </Grid>

        {/* Address 2 */}
        <Grid item xs={12} md={4}>
          <Paper
            elevation={3}
            sx={{
              padding: 3,
              borderRadius: 2,
              boxShadow: "0 0 10px 4px rgba(239, 154, 154, 0.5)",
            }}
          >
            <Typography
              variant="h6"
              sx={{ color: "#1976d2", mb: 2, fontSize: "1.2rem" }}
            >
              <LocationOn
                sx={{
                  verticalAlign: "middle",
                  marginRight: 1,
                  color: "#145ea8",
                }}
              />
              Address 2
            </Typography>
            <Divider sx={{ mb: 2, bgcolor: "#1976d2" }} />
            <Typography variant="body2" sx={{ lineHeight: 1.6 }}>
              203, Bharadhidasan Nagar, <br />
              Arunagiri Puram, <br />
              Thiruvannamalai - 606601.
            </Typography>
          </Paper>
        </Grid>
      </Grid>

      {/* Social Media Section */}
      <Box sx={{ mt: 6, textAlign: "center" }}>
        <Typography
          variant="h6"
          sx={{
            color: "#022e69",
            mb: 3,
            fontWeight: 600,
            fontSize: { xs: "1.6rem", md: "2rem" },
          }}
        >
          FOLLOW US
        </Typography>
        <Grid container justifyContent="center" spacing={2}>
          <Grid item>
            <IconButton
              aria-label="Instagram"
              href="https://instagram.com/smineservices"
              target="_blank"
              rel="noopener noreferrer"
              sx={{
                bgcolor: "#E4405F",
                color: "#fff",
                "&:hover": { bgcolor: "#C13584" },
              }}
            >
              <Instagram />
            </IconButton>
          </Grid>
          {/* <Grid item>
            <IconButton
              aria-label="LinkedIn"
              href="https://linkedin.com"
              target="_blank"
              rel="noopener noreferrer"
              sx={{
                bgcolor: "#0077b5",
                color: "#fff",
                "&:hover": { bgcolor: "#005582" },
              }}
            >
              <LinkedIn />
            </IconButton>
          </Grid> */}
          {/* <Grid item>
            <IconButton
              aria-label="Facebook"
              href="https://facebook.com"
              target="_blank"
              rel="noopener noreferrer"
              sx={{
                bgcolor: "#4267B2",
                color: "#fff",
                "&:hover": { bgcolor: "#3b5998" },
              }}
            >
              <Facebook />
            </IconButton>
          </Grid> */}
        </Grid>
      </Box>
    </Box>
  );
};

export default ContactUs;
