import React, { useState } from "react";
import { useParams } from "react-router-dom";
import { services } from "../../constants/services";
import { icons } from "../../constants/services/icons";
import {
  Box,
  Typography,
  Grid,
  Card,
  CardContent,
  Dialog,
  DialogTitle,
  DialogContent,
  IconButton,
  useTheme,
  useMediaQuery,
} from "@mui/material";
import CloseIcon from "@mui/icons-material/Close";
import SubService from "../subService";

const Service = () => {
  const { serviceName } = useParams();
  const [open, setOpen] = useState(false);
  const [selectedSubService, setSelectedSubService] = useState("");

  const service = services[serviceName];
  const serviceIcon = icons[serviceName];

  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down("sm")); // Mobile detection

  if (!service) {
    return (
      <Box
        sx={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          minHeight: "100vh",
          backgroundColor: "#f5f5f5",
        }}
      >
        <Typography variant="h4" color="error">
          Service not found
        </Typography>
      </Box>
    );
  }

  const handleSubServiceClick = (subService) => {
    const formattedSubService = subService
      .toLowerCase()
      .replace(/\s+/g, "-")
      .replace(/\//g, "or");

    setSelectedSubService(formattedSubService);
    setOpen(true);
  };

  return (
    <Box
      sx={{
        padding: isMobile ? 0 : "3rem 1.5rem", // Remove padding on mobile
        display: "flex",
        flexDirection: "column",
        alignItems: isMobile ? "stretch" : "center", // Remove centering on mobile
      }}
    >
      <Card
        sx={{
          width: "100%",
          maxWidth: "800px",
          boxShadow: 6,
          borderRadius: "16px",
          background: "linear-gradient(135deg, #E4D4D9, #B4C5E4)",
          color: "#333",
          marginBottom: "2rem",
          textAlign: "center",
        }}
      >
        <CardContent sx={{ textAlign: "center" }}>
          <Box
            sx={{
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              marginBottom: "1rem",
            }}
          >
            {serviceIcon && (
              <Box
                sx={{
                  width: "60px",
                  height: "60px",
                  borderRadius: "50%",
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                  border: "4px solid #0077b6",
                  backgroundColor: "#fff",
                  boxShadow: "0px 4px 6px rgba(0, 0, 0, 0.1)",
                }}
              >
                {serviceIcon.icon}
              </Box>
            )}

            <Typography
              variant="h4"
              sx={{
                fontWeight: "600",
                color: "#022e69",
                marginLeft: "1rem",
              }}
            >
              {service["Service name"]}
            </Typography>
          </Box>

          {service["Description"] && (
            <Box>
              {service["Description"].map((desc, index) => (
                <Typography
                  key={index}
                  variant="body1"
                  sx={{
                    color: "#555",
                    marginBottom: "0.5rem",
                    fontSize: "1rem",
                  }}
                >
                  {desc}
                </Typography>
              ))}
            </Box>
          )}
        </CardContent>
      </Card>

      {service["Sub services"] && service["Sub services"].length > 0 && (
        <Grid
          container
          spacing={3}
          justifyContent="center"
          alignItems="stretch"
        >
          {service["Sub services"].map((subService, index) => (
            <Grid item xs={12} sm={6} md={4} key={index}>
              <Card
                sx={{
                  cursor: "pointer",
                  borderRadius: "12px",
                  transition: "transform 0.3s, box-shadow 0.3s",
                  backgroundColor: "#ffffff",
                  boxShadow: "0 0 10px 4px rgba(239, 154, 154, 0.5)", // Initial pastel shadow (soft pink)
                  "&:hover": {
                    transform: "scale(1.05)",
                    boxShadow: "0 0 15px 6px rgba(239, 154, 154, 0.8)", // Darker shadow on hover
                  },
                  display: "flex",
                  flexDirection: "column",
                  justifyContent: "center", // Center content vertically
                  height: "100%", // Ensure full height to match the tallest card
                }}
                onClick={() => handleSubServiceClick(subService)}
              >
                <CardContent
                  sx={{
                    textAlign: "center",
                    padding: "1rem",
                  }}
                >
                  <Typography
                    variant="h6"
                    sx={{
                      fontWeight: "bold",
                      color: "#0077b6",
                    }}
                  >
                    {subService}
                  </Typography>
                </CardContent>
              </Card>
            </Grid>
          ))}
        </Grid>
      )}

      <Dialog
        open={open}
        onClose={() => setOpen(false)}
        fullWidth
        maxWidth="sm"
      >
        <DialogTitle>
          <IconButton
            sx={{
              position: "absolute",
              right: 16,
              top: 16,
              color: "grey",
            }}
            onClick={() => setOpen(false)}
          >
            <CloseIcon />
          </IconButton>
        </DialogTitle>
        <DialogContent>
          <SubService
            serviceName={serviceName}
            subService={selectedSubService}
          />
        </DialogContent>
      </Dialog>
    </Box>
  );
};

export default Service;
