import React, { useState } from "react";
import { Link } from "react-router-dom"; // Import Link from react-router-dom
import NavBar from "../navbar";
import {
  AppBar,
  Toolbar,
  Container,
  Typography,
  IconButton,
} from "@mui/material";
import MenuIcon from "@mui/icons-material/Menu";

const Header = () => {
  const [anchorEl, setAnchorEl] = useState(null);

  const handleMenuOpen = (event) => {
    setAnchorEl(event.currentTarget); // Set anchor element when menu icon is clicked
  };

  const handleMenuClose = () => {
    setAnchorEl(null); // Close the menu
  };

  return (
    <AppBar position="sticky" sx={{ backgroundColor: "#005976" }}>
      <Toolbar>
        <Container
          maxWidth="lg"
          sx={{
            display: "flex",
            alignItems: "center",
            justifyContent: "space-between",
          }}
        >
          {/* Organization Name - Wrapped in Link */}
          <Link to="/" style={{ textDecoration: "none" }}>
            <Typography variant="h6" sx={{ color: "white" }}>
              smineservices
            </Typography>
          </Link>

          {/* NavBar in the Center */}
          <NavBar handleMenuClose={handleMenuClose} anchorEl={anchorEl} />

          {/* Menu Icon for Small Screens */}
          <IconButton
            color="inherit"
            edge="end"
            onClick={handleMenuOpen}
            sx={{ display: { xs: "block", sm: "none" } }}
          >
            <MenuIcon />
          </IconButton>
        </Container>
      </Toolbar>
    </AppBar>
  );
};

export default Header;
