import React from "react";
import { Box, Typography, Grid, Divider, useMediaQuery } from "@mui/material";

// Sample SVG images imports (same as before)
import AboutSvg from "../../assets/about.svg";
import ServicesSvg from "../../assets/services.svg";
import VisionSvg from "../../assets/vision.svg";
import MissionSvg from "../../assets/mission.svg";

// Text and SVG styling
const svgStyles = {
  width: "100%",
  height: "auto",
  maxWidth: "250px",
};

const sectionTitleStyles = {
  color: "#333",
  fontWeight: 700,
  fontSize: "2rem",
  marginBottom: "1rem",
  textTransform: "uppercase",
};

const paragraphStyles = {
  color: "#555",
  lineHeight: 1.8,
  fontSize: "1rem",
  maxWidth: "800px",
  marginBottom: "1.5rem",
};

// const quoteStyles = {
//   color: "#555",
//   fontStyle: "italic",
//   fontSize: "1.25rem",
//   marginTop: "1rem",
// };

const Home = () => {
  // Check if the screen size is mobile (small)
  const isMobile = useMediaQuery("(max-width:600px)");

  const content = (
    <>
      {/* About Section */}
      <Grid container spacing={4} sx={{ mb: 8 }}>
        <Grid item xs={12} sm={6}>
          <Typography variant="h4" sx={sectionTitleStyles} gutterBottom>
            About Us
          </Typography>
          <Typography variant="body1" sx={paragraphStyles} paragraph>
            Our Web design agency and e-services centre focuses on providing an
            exceptional user experience right from the start. Visitors are
            greeted with vibrant visuals that showcase our offerings in logo
            design, web development, graphic design, and CSC government
            services.
          </Typography>
          {/* <Typography variant="h6" sx={quoteStyles}>
            We are here to help you
          </Typography> */}
        </Grid>
        <Grid
          item
          xs={12}
          sm={6}
          sx={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <img src={AboutSvg} alt="About Icon" style={svgStyles} />
        </Grid>
      </Grid>

      <Divider sx={{ mb: 8 }} />

      {/* Services Overview Section */}
      <Grid container spacing={4} sx={{ mb: 8 }}>
        <Grid
          item
          xs={12}
          sm={6}
          sx={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <img src={ServicesSvg} alt="Services Icon" style={svgStyles} />
        </Grid>
        <Grid item xs={12} sm={6}>
          <Typography variant="h4" sx={sectionTitleStyles} gutterBottom>
            Our Services
          </Typography>
          <Typography variant="body1" sx={paragraphStyles} paragraph>
            Explore a wide range of services designed to assist you with your
            business, legal, and financial needs. Our expert solutions cover
            everything from registrations and tax services to insurance,
            intellectual property, and more, ensuring that your requirements are
            handled with professionalism and care.
          </Typography>
        </Grid>
      </Grid>

      <Divider sx={{ mb: 8 }} />

      {/* Vision Section */}
      <Grid container spacing={4} sx={{ mb: 8 }}>
        <Grid item xs={12} sm={6}>
          <Typography variant="h4" sx={sectionTitleStyles} gutterBottom>
            Vision
          </Typography>
          <Typography variant="body1" sx={paragraphStyles} paragraph>
            Your satisfaction is our goal, making sure you receive quality
            without compromising on time or cost. We provide the best service
            possible while ensuring timely deliveries. This space should reflect
            our brand's personality while clearly communicating what we offer.
            We will make connections that lead to lasting relationships with
            customers seeking better service and support.
          </Typography>
        </Grid>
        <Grid
          item
          xs={12}
          sm={6}
          sx={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <img src={VisionSvg} alt="Vision Icon" style={svgStyles} />
        </Grid>
      </Grid>

      <Divider sx={{ mb: 8 }} />

      {/* Mission Section */}
      <Grid container spacing={4}>
        <Grid
          item
          xs={12}
          sm={6}
          sx={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <img src={MissionSvg} alt="Mission Icon" style={svgStyles} />
        </Grid>
        <Grid item xs={12} sm={6}>
          <Typography variant="h4" sx={sectionTitleStyles} gutterBottom>
            Mission
          </Typography>
          <Typography variant="body1" sx={paragraphStyles} paragraph>
            Our website design is the first impression visitors get when they
            arrive at your site. Its where curiosity meets clarity, guiding
            potential clients through their journey. A well-designed website
            highlights your key services. Eye-catching graphics paired with
            concise text can draw in users and keep them engaged.
          </Typography>
        </Grid>
      </Grid>
    </>
  );

  return isMobile ? (
    content
  ) : (
    <Box
      sx={{
        p: 4,
        // backgroundColor: "#f4f6f8"
      }}
    >
      {content}
    </Box>
  );
};

export default Home;
