import React, { useState } from "react";
import {
  TextField,
  Button,
  Box,
  Snackbar,
  Alert,
  Typography,
  CircularProgress, // Import CircularProgress for loading spinner
} from "@mui/material";
import axios from "axios";

const ContactForm = () => {
  const [open, setOpen] = useState(false);
  const [severity, setSeverity] = useState(""); // to set the type of alert (success/error)
  const [message, setMessage] = useState(""); // to display the message
  const [formErrors, setFormErrors] = useState({
    Name: "",
    PhoneNumber: "",
    Email: "",
    DetailsOfEnquiry: "",
    AlternatePhoneNumber: "",
  });
  const [loading, setLoading] = useState(false); // Added loading state

  const validatePhoneNumber = (phoneNumber) => {
    const phoneRegex = /^[+]?[0-9]{10,13}$/; // Basic phone number validation
    return phoneRegex.test(phoneNumber);
  };

  const handleSubmit = async (event) => {
    event.preventDefault(); // Prevent the default form submission
    const formData = new FormData(event.target);
    const data = {
      Name: formData.get("Name"),
      DetailsOfEnquiry: formData.get("DetailsOfEnquiry"),
      PhoneNumber: formData.get("PhoneNumber"),
      AlternatePhoneNumber: formData.get("AlternatePhoneNumber"),
      Email: formData.get("Email"),
    };

    // Basic validation
    let errors = {};

    // Name validation
    if (!data.Name) errors.Name = "Name is required.";

    // Phone number validation
    if (!data.PhoneNumber) {
      errors.PhoneNumber = "Phone number is required.";
    } else if (!validatePhoneNumber(data.PhoneNumber)) {
      errors.PhoneNumber = "Please enter a valid phone number.";
    }

    // Alternate phone number validation
    if (
      data.AlternatePhoneNumber &&
      !validatePhoneNumber(data.AlternatePhoneNumber)
    ) {
      errors.AlternatePhoneNumber =
        "Please enter a valid alternate phone number.";
    }

    // Email validation
    if (!data.Email) {
      errors.Email = "Email is required.";
    } else if (!/\S+@\S+\.\S+/.test(data.Email)) {
      errors.Email = "Please enter a valid email.";
    }

    // Details of enquiry validation
    if (!data.DetailsOfEnquiry)
      errors.DetailsOfEnquiry = "Details of enquiry are required.";

    setFormErrors(errors);

    if (Object.keys(errors).length > 0) return; // Don't submit if there are errors

    setLoading(true); // Show loading spinner

    try {
      // API call to submit the form data
      // const formSubmitPromise = axios.post(
      //   "https://script.google.com/macros/s/AKfycbxHQXVJZjTnlTp5EmpMn57G3uQJAJnSktOvQFEljBeRSk59j9dJ0MMgPP_6wMLU8z_a1Q/exec",
      //   new URLSearchParams(data)
      // );

      // API call to send an email
      const sendEmailPromise = axios.post(
        "https://yzotghl67k.execute-api.us-east-1.amazonaws.com/send-email",
        data
      );

      // Wait for both promises to resolve
      const [
        // formResponse,
        emailResponse,
      ] = await Promise.all([
        // formSubmitPromise,
        sendEmailPromise,
      ]);

      if (
        // formResponse.status === 200 &&
        emailResponse.status === 200
      ) {
        setSeverity("success");
        setMessage("Form submitted and email sent successfully!");
        setOpen(true); // Open the Snackbar
        // Clear form values after successful submission
        event.target.reset();
      } else {
        setSeverity("error");
        setMessage(
          "Failed to submit the form or send email. Please try again."
        );
        setOpen(true); // Open the Snackbar
      }
    } catch (error) {
      console.error("Error submitting form:", error);
      setSeverity("error");
      setMessage("An error occurred. Please try again later.");
      setOpen(true); // Open the Snackbar
    } finally {
      setLoading(false); // Hide loading spinner after submission
    }
  };

  const handleClose = () => {
    setOpen(false); // Close the Snackbar
  };

  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "column",
        gap: 2,
        maxWidth: 600,
        margin: "0 auto",
        padding: 3,
        borderRadius: 2,
        boxShadow: 3,
        backgroundColor: "#fff",
        "@media (max-width: 600px)": {
          maxWidth: "100%", // Ensure form takes full width on small screens
          padding: 2,
        },
        "@media (max-width: 400px)": {
          maxWidth: "90%", // Prevent the form from being too small
        },
      }}
    >
      <Typography variant="body1" color="textSecondary" paragraph>
        Please fill in the form below to submit your enquiry. <br />
        You will receive a call on the phone number you provided in this form.
      </Typography>

      <Box
        component="form"
        onSubmit={handleSubmit}
        sx={{
          display: "flex",
          flexDirection: "column",
          gap: 2,
          alignItems: "center", // Keep form elements centered
        }}
      >
        <TextField
          name="Name"
          type="text"
          label="Name"
          placeholder="Enter your name"
          required
          fullWidth
          error={!!formErrors.Name}
          helperText={formErrors.Name}
          autoComplete="off" // Disable suggestions
          sx={{
            backgroundColor: "#f9f9f9",
            borderRadius: 1,
            "& .MuiInputBase-root": {
              padding: "12px",
            },
            "@media (max-width: 600px)": {
              width: "100%", // Ensure full-width on small screens
            },
          }}
        />
        <TextField
          name="DetailsOfEnquiry"
          type="text"
          label="Details of Enquiry"
          placeholder="Enter details of your enquiry"
          required
          fullWidth
          multiline
          rows={4}
          autoComplete="off" // Disable suggestions
          sx={{
            backgroundColor: "#f9f9f9",
            borderRadius: 1,
            "& .MuiInputBase-root": {
              padding: "12px",
            },
            "@media (max-width: 600px)": {
              width: "100%", // Full width for mobile view
            },
          }}
        />
        <TextField
          name="PhoneNumber"
          type="tel"
          label="Phone Number"
          placeholder="Enter your phone number"
          required
          fullWidth
          error={!!formErrors.PhoneNumber}
          helperText={formErrors.PhoneNumber}
          autoComplete="off" // Disable suggestions
          sx={{
            backgroundColor: "#f9f9f9",
            borderRadius: 1,
            "& .MuiInputBase-root": {
              padding: "12px",
            },
            "@media (max-width: 600px)": {
              width: "100%", // Full width on mobile
            },
          }}
        />
        <TextField
          name="AlternatePhoneNumber"
          type="tel"
          label="Alternate Phone Number"
          placeholder="Enter an alternate phone number"
          fullWidth
          autoComplete="off" // Disable suggestions
          sx={{
            backgroundColor: "#f9f9f9",
            borderRadius: 1,
            "& .MuiInputBase-root": {
              padding: "12px",
            },
            "@media (max-width: 600px)": {
              width: "100%", // Full width for smaller screens
            },
          }}
        />
        <TextField
          name="Email"
          type="email"
          label="Email"
          placeholder="Enter your email"
          required
          fullWidth
          error={!!formErrors.Email}
          helperText={formErrors.Email}
          autoComplete="off" // Disable suggestions
          sx={{
            backgroundColor: "#f9f9f9",
            borderRadius: 1,
            "& .MuiInputBase-root": {
              padding: "12px",
            },
            "@media (max-width: 600px)": {
              width: "100%", // Full width for mobile devices
            },
          }}
        />

        {/* Centered Submit Button */}
        <Button
          type="submit"
          variant="contained"
          color="primary"
          sx={{
            padding: "8px 16px",
            fontSize: "14px",
            borderRadius: 1,
            textTransform: "none",
            backgroundColor: "#0890b9",
            color: "white",
            "&:hover": {
              backgroundColor: "#0890b9",
              transform: "scale(1.05)",
              transition: "transform 0.2s ease-in-out",
            },
            width: "120px", // Fixed width
            marginTop: 2,
          }}
          disabled={loading} // Disable the button when loading
        >
          {loading ? <CircularProgress size={24} color="inherit" /> : "Submit"}{" "}
          {/* Display loading spinner */}
        </Button>
      </Box>

      {/* Snackbar for toast notifications */}
      <Snackbar
        open={open}
        autoHideDuration={6000}
        onClose={handleClose}
        anchorOrigin={{ vertical: "top", horizontal: "right" }}
      >
        <Alert onClose={handleClose} severity={severity} sx={{ width: "100%" }}>
          {message}
        </Alert>
      </Snackbar>
    </Box>
  );
};

export default ContactForm;
