import React from "react";
import { useNavigate } from "react-router-dom";
import {
  Grid,
  Card,
  CardContent,
  Box,
  Typography,
  useMediaQuery,
} from "@mui/material";
import { services } from "../../constants/services";
import { icons } from "../../constants/services/icons";

const Services = () => {
  const navigate = useNavigate();
  const isMobile = useMediaQuery("(max-width:600px)"); // Detect mobile view

  const handleServiceClick = (serviceKey) => {
    navigate(`/service/${serviceKey}`);
  };

  return (
    <Box
      sx={{
        // backgroundColor: isMobile ? "transparent" : "#f9f9f9",
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        justifyContent: "center",
        padding: isMobile ? "0" : "2rem 1rem",
      }}
    >
      <Grid container spacing={3} justifyContent="center">
        {Object.entries(services).map(([key, service], index) => (
          <Grid item xs={12} sm={6} md={4} key={key}>
            <Card
              sx={{
                cursor: "pointer",
                boxShadow: 4,
                borderRadius: "16px",
                transition: "transform 0.3s, box-shadow 0.3s",
                "&:hover": {
                  transform: "scale(1.05)",
                  boxShadow: 8,
                },
                background: "#ffffff",
                position: "relative",
                height: "100%",
                border: "1px solid #f1f1f1",
              }}
              onClick={() => handleServiceClick(key)}
            >
              <Box
                sx={{
                  position: "absolute",
                  top: 0,
                  left: 0,
                  right: 0,
                  height: "100%",
                  background: "linear-gradient(135deg, #E4D4D9, #B4C5E4)",
                  zIndex: 1,
                }}
              />
              <CardContent
                sx={{
                  padding: "3rem 2rem",
                  textAlign: "center",
                  position: "relative",
                  zIndex: 2,
                  display: "flex",
                  flexDirection: "column",
                  justifyContent: "space-between",
                  height: "100%",
                  overflow: "hidden",
                }}
              >
                <Box
                  sx={{
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "flex-start",
                    gap: "1rem",
                    flexDirection: isMobile
                      ? index % 2 === 0
                        ? "row"
                        : "row-reverse" // Alternate direction for mobile view
                      : "row",
                  }}
                >
                  {/* Icon on the left or right based on index */}
                  <Box
                    sx={{
                      width: "110px",
                      height: "80px",
                      borderRadius: "50%",
                      backgroundColor: "#ffffff",
                      display: "flex",
                      alignItems: "center",
                      justifyContent: "center",
                      boxShadow: "0px 4px 8px rgba(0, 0, 0, 0.1)",
                      transition: "background-color 0.3s",
                      "&:hover": {
                        backgroundColor: "#f0f0f0",
                      },
                    }}
                  >
                    {icons[key].icon}
                  </Box>

                  <Box
                    sx={{
                      display: "flex",
                      flexDirection: "column",
                      justifyContent: "center",
                      gap: "0.5rem",
                    }}
                  >
                    <Typography
                      variant="h6"
                      sx={{
                        color: "#333",
                        fontWeight: "bold",
                        fontSize: "1.3rem",
                        textAlign: isMobile ? "center" : "left", // Center text on mobile
                      }}
                    >
                      {service["Service name"]}
                    </Typography>

                    <Typography
                      variant="body2"
                      sx={{
                        color: "#555",
                        fontSize: "0.875rem",
                        textAlign: isMobile ? "center" : "left", // Center text on mobile
                      }}
                    >
                      {icons[key].description}
                    </Typography>
                  </Box>
                </Box>
              </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>
    </Box>
  );
};

export default Services;
