import { useState } from "react";
import {
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  Box,
  Typography,
  Grid,
  IconButton,
} from "@mui/material";
import CloseIcon from "@mui/icons-material/Close";
import RefundPolicy from "../../pages/refundPolicy";
import PrivacyPolicy from "../../pages/privacyPolicy";
import TermsAndConditions from "../../pages/termsAndConditions";

const Footer = () => {
  const [dialogTitle, setDialogTitle] = useState("");
  const [dialogContent, setDialogContent] = useState(null);
  const [isDialogOpen, setIsDialogOpen] = useState(false);

  const openDialog = (title, content) => {
    setDialogTitle(title);
    setDialogContent(content);
    setIsDialogOpen(true);
  };

  const closeDialog = () => {
    setIsDialogOpen(false);
  };

  return (
    <Box
      component="footer"
      sx={{
        textAlign: "center",
        py: 3,
        mt: "auto",
        backgroundColor: "#02789e",
        color: "white",
      }}
    >
      <Typography variant="body2" color="inherit">
        &copy; 2024 smintech24. All rights reserved.
      </Typography>
      <Grid container spacing={2} justifyContent="center" sx={{ mt: 2 }}>
        <Grid item>
          <Button
            variant="text"
            onClick={() => openDialog("Refund Policy", <RefundPolicy />)}
            sx={{ color: "white" }}
          >
            Refund Policy
          </Button>
        </Grid>
        <Grid item>
          <Button
            variant="text"
            onClick={() => openDialog("Privacy Policy", <PrivacyPolicy />)}
            sx={{ color: "white" }}
          >
            Privacy Policy
          </Button>
        </Grid>
        <Grid item>
          <Button
            variant="text"
            onClick={() =>
              openDialog("Terms and Conditions", <TermsAndConditions />)
            }
            sx={{ color: "white" }}
          >
            Terms and Conditions
          </Button>
        </Grid>
      </Grid>

      <Dialog
        open={isDialogOpen}
        onClose={closeDialog}
        TransitionProps={{
          onExited: () => {
            setDialogContent(null);
            setDialogTitle("");
          },
        }}
        fullWidth
        maxWidth="sm"
      >
        <DialogTitle>
          {dialogTitle}
          <IconButton
            aria-label="close"
            onClick={closeDialog}
            sx={{
              position: "absolute",
              right: 8,
              top: 8,
              color: (theme) => theme.palette.grey[500],
            }}
          >
            <CloseIcon />
          </IconButton>
        </DialogTitle>
        <DialogContent dividers>{dialogContent}</DialogContent>
      </Dialog>
    </Box>
  );
};

export default Footer;
