import React from "react";
import { subServices } from "../../constants/subServices";
import { Box, Typography, Divider, Button } from "@mui/material";

const SubService = ({ serviceName, subService }) => {
  const service = subServices[serviceName];
  const subServiceDetails = service ? service[subService] : null;

  const handleApplyNow = () => {
    const phoneNumber = "9385701440";
    const message = `Hello, I am interested in applying for the ${subService} service under ${serviceName}. Please provide more details.`;
    const encodedMessage = encodeURIComponent(message);

    window.open(
      `https://wa.me/${phoneNumber}?text=${encodedMessage}`,
      "_blank"
    );
  };

  if (!subServiceDetails) {
    return (
      <Box
        sx={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          height: "100%",
          textAlign: "center",
        }}
      >
        <Typography variant="h5" color="error">
          Sub-service not found
        </Typography>
      </Box>
    );
  }

  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "column",
        justifyContent: "flex-start",
        alignItems: "center",
        height: "100%",
        overflowY: "auto",
        padding: "1rem",
      }}
    >
      <Box sx={{ maxWidth: "800px", width: "100%" }}>
        {Object.entries(subServiceDetails)
          .filter(([key]) => key !== "Name")
          .map(([categoryName, categoryData], index) => (
            <Box key={index} sx={{ marginBottom: "2rem" }}>
              <Typography
                variant="h5"
                sx={{
                  fontWeight: "bold",
                  mb: 1,
                  color: "#09607a", // Using MUI theme color
                  // textTransform: "uppercase", // Capitalize text
                  letterSpacing: "0.1rem", // Increase letter spacing
                  // borderBottom: "3px solid", // Add underline effect
                  // borderColor: "primary.light", // Border color
                  paddingBottom: "0.5rem",
                }}
              >
                {categoryName}
              </Typography>
              <Divider sx={{ mb: 2 }} />

              {categoryData["Description"] && (
                <ul style={{ paddingLeft: "1.5rem", marginBottom: "1rem" }}>
                  {categoryData["Description"].map(
                    (criteria, criteriaIndex) => (
                      <li key={criteriaIndex}>
                        <Typography variant="body1">{criteria}</Typography>
                      </li>
                    )
                  )}
                </ul>
              )}

              {categoryData["Required documents"] && (
                <Box>
                  {Object.entries(categoryData["Required documents"]).map(
                    ([docType, docInfo], docIndex) => (
                      <Box key={docIndex} sx={{ mb: 2 }}>
                        <Typography
                          variant="h6"
                          sx={{
                            fontWeight: "bold",
                            mb: 1,
                            color: "#0890b9", // Secondary color for document type
                          }}
                        >
                          {docType}
                        </Typography>

                        {docInfo.Documents && (
                          <ul style={{ paddingLeft: "1.5rem" }}>
                            {docInfo.Documents.map((document, docIdx) => (
                              <li key={docIdx}>
                                <Typography variant="body2">
                                  {document}
                                </Typography>
                              </li>
                            ))}
                          </ul>
                        )}

                        {docInfo.Note && (
                          <Typography
                            variant="body2"
                            color="textSecondary"
                            sx={{ mt: 1 }}
                          >
                            <b>Note: </b>
                            {docInfo.Note}
                          </Typography>
                        )}
                      </Box>
                    )
                  )}
                </Box>
              )}
            </Box>
          ))}

        {/* Apply Now Button */}
        <Box sx={{ textAlign: "center", marginTop: "2rem" }}>
          <Button
            variant="contained"
            sx={{ backgroundColor: "#0890b9", color: "white" }}
            color="primary"
            onClick={handleApplyNow}
          >
            Apply Now
          </Button>
        </Box>
      </Box>
    </Box>
  );
};

export default SubService;
