import React from "react";
import {
  Grid,
  IconButton,
  Typography,
  Menu,
  MenuItem,
} from "@mui/material";
import { Link, useLocation } from "react-router-dom";
import HomeIcon from "@mui/icons-material/Home";
import BuildIcon from "@mui/icons-material/Build";
import ContactMailIcon from "@mui/icons-material/ContactMail";
import WorkIcon from "@mui/icons-material/Work";

const NavBar = ({ handleMenuClose, anchorEl }) => {
  const location = useLocation(); // Get the current path

  // Function to check if a link is active
  const isActive = (path) => location.pathname === path;

  // Navigation items with icons and labels
  const navItems = [
    { path: "/", label: "Home", icon: <HomeIcon fontSize="medium" /> },
    {
      path: "/services",
      label: "Services",
      icon: <BuildIcon fontSize="medium" />,
    },
    {
      path: "/contact",
      label: "Contact",
      icon: <ContactMailIcon fontSize="medium" />,
    },
    {
      path: "/enquiry",
      label: "Enquiry",
      icon: <WorkIcon fontSize="medium" />,
    },
  ];

  return (
    <>
      {/* For larger screens: Display nav items in a grid */}
      <Grid
        container
        alignItems="center"
        justifyContent="center"
        sx={{
          py: 1,
          px: 2,
          backgroundColor: "#005976",
          color: "white",
          flexWrap: "nowrap", // Prevent wrapping to the next line
          gap: 4, // Space between items
          display: { xs: "none", sm: "flex" }, // Hide on small screens
        }}
      >
        {navItems.map((item) => (
          <IconButton
            key={item.path}
            component={Link}
            to={item.path}
            sx={{
              color: isActive(item.path) ? "#ffeb3b" : "white",
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
            }}
          >
            {/* Conditionally render either icon or label based on screen size */}
            <Typography
              variant="button"
              sx={{ fontWeight: isActive(item.path) ? "bold" : "normal" }}
            >
              {item.label}
            </Typography>
          </IconButton>
        ))}
      </Grid>

      {/* For small screens: Show Menu Popup */}
      <Menu
        anchorEl={anchorEl}
        open={Boolean(anchorEl)}
        onClose={handleMenuClose}
        sx={{
          display: { xs: "block", sm: "none" },
          "& .MuiPaper-root": {
            width: 250, // Adjust width to make the popup bigger
          },
        }}
      >
        {navItems.map((item) => (
          <MenuItem
            key={item.path}
            component={Link}
            to={item.path}
            onClick={handleMenuClose} // Close the menu after clicking an item
            sx={{
              color: isActive(item.path) ? "#ffeb3b" : "black",
            }}
          >
            {item.label}
          </MenuItem>
        ))}
      </Menu>
    </>
  );
};

export default NavBar;
